import React, { Component } from 'react';
import { connect } from 'react-redux';

import Aux from '../../hoc/Aux/Aux';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import axios from '../../axios-orders';
import classes from './ChartLine.css'
import Chart from 'chart.js'



let myChart;


export class chartLine extends Component {

    renderChart = () => {
        let ctx = document.getElementById('myChart').getContext('2d');
        myChart = new Chart(ctx, {
            type: 'line',
            data: {
                labels: ['20-07-2020', '21-07-2020', '22-07-2020', '23-07-2020', '24-07-2020', '25-07-2020', '26-07-2020', '27-07-2020'],
                datasets: this.props.chartData,
            },
            options: {
                responsive: true,
                hoverMode: 'index',
                stacked: false,
                legend: {
                    display: false,
                },
                scales: {
                    yAxes: [{
                        display: true,
                        id: 'y-axis-1',
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white"
                        },
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white"
                        },
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                    }],
                },
                tooltips: {
                    // yAlign: 'bottom',
                    bodySpacing: 6,
                    titleSpacing: 6,
                    footerSpacing: 6,
                    xPadding: 12,
                    yPadding: 12,
                    titleFontSize: 14,
                    bodyFontSize: 14,
                    backgroundColor: "black",
                    callbacks: {
                        label: function (tooltipItems, data) {
                            let text = `${data.datasets[tooltipItems.datasetIndex].label}  :  ${tooltipItems.value}`
                            return `  ${text.charAt(0).toUpperCase() + text.slice(1)}`
                        },
                    },
                    footerFontStyle: 'normal'
                },
            }
        });



    }

    componentDidMount() {
        this.renderChart();
    }

    componentWillReceiveProps() {
        myChart.data.datasets = this.props.chartData;
        myChart.update();
    }


    render() {
        return (
            <Aux>
                <div className={classes.ChartLine}>
                    <canvas id="myChart"></canvas>
                    <p style={{ visibility: 'hidden' }}>{`${this.props.chartData}`}</p>
                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        chartData: state.chartLine.chartData,
        up: state.chartLine.up,
    };
};

const mapDispatchToProps = dispatch => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(chartLine, axios));


