import React, { Component } from 'react';
import classes from './SideItem.css';
import Aux from '../../../../hoc/Aux/Aux';


class sideItem extends Component {


    state = {
        active: this.props.active
    }

    test = () => {
        this.props.clicked();
        this.setState(prevState => {
            return { active: !prevState.active };
        });
    }

    render() {
        let attachedClassesToItem;
        this.state.active
            ? attachedClassesToItem = [classes.SideItem, classes.SideItemActive]
            : attachedClassesToItem = [classes.SideItem];

        let itemToRender = (<Aux>
            <li style={{ borderColor: this.props.color, borderStyle: 'none none none solid', background: '#292927'}} className={attachedClassesToItem.join(' ')} onClick={this.test} >
                {this.props.name}
            </li >
        </Aux>)

        if (!this.state.active) {
            itemToRender = (<Aux>
                <li className={attachedClassesToItem.join(' ')} onClick={this.test} >
                    {this.props.name}
                </li >
            </Aux>)
        }

        return (
            itemToRender
        );
    }
}


export default sideItem;


